const initState = {
    visible:false,
    
};

export default {
    namespace:'App',
    state:initState,
    subscriptions:{},
    effects:{

    },
    reducers:{
        showDrawer(state){
                state.visible =true;
                return {...state}
        },
        onClose(state){
            state.visible =false;
            return {...state}
        },

    }
    
   
}